import swiperImage from "@/components/index/swiper-image.vue";
import indexNav from "@/components/index/index-nav.vue";
import threeAdv from "@/components/index/three-adv.vue";
import card from "@/components/common/card.vue";
import commonList from "@/components/common/common-list.vue";
export default {
	components: {
		swiperImage,
		indexNav,
		threeAdv,
		card,
		commonList
	},
	data() {
		return {
			scrollinto:"",
			scrollH:500,
			tabIndex:0,
			tabBars: [],
			newsitems:[]
			
		
		}
	},
	onLoad() {
		// 获取可视区域高度
		uni.getSystemInfo({
			success: (res) => {




				let navbarH = uni.upx2px(90)

				this.scrollH = res.windowHeight - uni.upx2px(82) - navbarH
			}
		})
		// 初始化事件
		this.__init()
	},
	methods: {
		// 初始化事件
		__init(){
			// 获取顶部选项卡
			this.$H.get('/index_category/data').then((res)=>{
				this.tabBars = res.category
				// 根据顶部选项卡生成页面
				let arr = []
				for (var i = 0; i < this.tabBars.length; i++) {
					let firstLoad = i === 0 ? 'after' : 'before'
					let obj = {
						list:[],
						// 1.上拉加载更多 2.加载中... 3.没有更多了
						loadtext:"上拉加载更多",
						// 首次加载：before加载前，after加载后，ing加载中
						firstLoad:firstLoad
					}
					// 获取首屏数据
					if (i === 0) {
						obj.list = res.data
					}
					
					arr.push(obj)
				}
				this.newsitems = arr
			})
		},
		// 切换选项卡
		changeTab(index){
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index
			this.scrollinto = 'tab'+index
			if (this.newsitems[index].firstLoad === 'after') {
				return;
			}
			this.addData()
		},
		// 监听滑动列表
		onChangeTab(e){
			this.changeTab(e.detail.current)
		},
		// 加载数据
		async addData(callback = false){
			// 拿到当前索引
			let index = this.tabIndex
			let obj = this.newsitems[index]
			// 拿到当前分类id
			let id = this.tabBars[index].id
			// 拿到当前分类的分页数
			let page = Math.ceil(obj.list.length/5) + 1
			
			// 请求前
			if (page === 1) {
				obj.loadtext = 'ing'
			}
			
			// 请求数据
			let data = await this.$H.get('/index_category/'+id+'/data/'+page)
			
			// 请求完数据
			if (page === 1) {
				obj.firstLoad = 'after'
			}
			
			if (data) {
				// 赋值
				obj.list = [...obj.list,...data]
				obj.loadtext = data.length < 5 ? '没有更多了' : '上拉加载更多'
			}
			// 执行回调函数
			if (typeof callback === 'function') {
				callback()
			}
		},
		// 上拉加载更多
		loadmore(index){
			let item = this.newsitems[index]
			// 是否处于可加载状态
			if (item.loadtext !== '上拉加载更多') return;
			// 模拟加载
			item.loadtext = '加载中...'
			
			this.addData(()=>{
				uni.showToast({
					title:'加载成功'
				})
			})
		}
	}
}
